import ServiceIconWrapper from '../components/ServiceIconWrapper';

export default function Airtime2Cash({ sx, ...other }) {
  return (
    <ServiceIconWrapper
      gradient="linear-gradient(135deg, #01b875 0%, #016647 100%)"
      sx={sx}
      {...other}
    >
      <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
        <path d="M17 10h-10l-4 4l4 4h10l4 -4l-4 -4z" />
        <path d="M7 10l4 -4l4 4" />
        <path d="M12 14v-4" />
        <path d="M12 6h.01" />
      </svg>
    </ServiceIconWrapper>
  );
}
